<template>
	<view class="sj-button" :style="btnStyle" @tap="btnClick">
		<uni-icons
			:type="btnIcon"
			:size="btnFontSize"
			:color="btnTextColor"
		></uni-icons>
		<text>{{ btnText }}</text>
		<view
			class="material"
			:class="[isMaterial ? 'material-show' : '']"
		></view>
	</view>
</template>
<script>
	let btnTimer = null;
	export default {
		data() {
			return {
				isMaterial: false,
			};
		},
		computed: {
			btnStyle() {
				const type = { base: "240rpx", lg: "100%", sm: "80rpx" };
				return {
					width: type[this.btnType],
					height: "80rpx",
					color: this.btnTextColor,
					backgroundColor: this.btnBgColor,
					fontSize: this.btnFontSize + "rpx",
					borderRadius: this.btnBorderRadius,
				};
			},
		},

		props: {
			// 按钮文本
			btnText: {
				type: String,
				default: "默认按钮",
			},

			// 按钮文本颜色
			btnTextColor: {
				type: String,
				default: "#fff",
			},

			// 按钮背景颜色
			btnBgColor: {
				type: String,
				default: "#f60",
			},

			// 按钮风格大小
			// base, lg, sm
			btnType: {
				type: String,
				default: "base",
			},

			// 按钮字体大小
			btnFontSize: {
				type: [String, Number],
				default: "32",
			},

			// 圆角
			btnBorderRadius: {
				type: [String, Number],
				default: "20rpx",
			},

			// 图标
			btnIcon: {
				type: String,
				default: "",
			},

			// 水波纹
			btnMaterial: {
				type: Boolean,
				default: false,
			},
		},

		methods: {
			btnClick() {
				if (this.btnMaterial) {
					this.isMaterial = true;
					clearTimeout(btnTimer);
					btnTimer = setTimeout(() => {
						this.isMaterial = false;
					}, 300);
				}
				this.$emit("on-click");
			},
		},
	};
</script>

<style lang="less" scoped>
	.sj-button {
		display: flex;
		flex-direction: row;
		align-items: center;
		justify-content: center;
		position: relative;
		overflow: hidden;

		.material {
			position: absolute;
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
			background: radial-gradient(circle, #fff 30%, transparent 10.2%);
			border-radius: 100%;
			transform: scale(10);
			opacity: 0;
			transition: all 0.8s;
		}

		.material-show {
			transform: scale(0);
			transition: 0s;
			opacity: 0.4;
		}
	}
</style>
